import btnComponent from '@/components/btnComponent.vue';

export default {
	components: {
		btnComponent
	},
	data() {
		return {
			fid: 0,
			avatar: '',
			clubname: '',
			// article: this.$app.getData('config').fanclub_notice,
		};
	},
	onShareAppMessage(e) {
		const shareType = e.target && e.target.dataset.share
		return this.$app.commonShareAppMessage(shareType)
	},
	onLoad(option) {
		this.fid = option.fid // 如果带上fid，表示修改粉丝团
		if (this.fid) this.loadData()
	},
	methods: {
		formSubmit(e) {
			const value = e.detail.value
			value['avatar'] = this.avatar
			if (!value['avatar']) {
				this.$app.toast('请上传粉丝团头像')
				return
			}
			if (!value['clubname']) {
				this.$app.toast('请输入粉丝团名字')
				return
			}
			if (!value['wx']) {
				this.$app.toast('输入负责人微信号')
				return
			}

			if (this.fid) {
				this.$app.modal(`需要消耗100钻石,是否继续？`, () => {
					value.fid = this.fid
					this.$app.request('fans/edit', value, res => {
						this.$app.toast('修改成功！')
						setTimeout(() => {
							uni.navigateBack()
						}, 500);
					}, 'POST', true)
				})
			} else {
				this.$app.request('fans/create', value, res => {
					this.$app.toast('创建成功！')
					setTimeout(() => {
						uni.navigateBack()
					}, 500);
				}, 'POST', true)
			}
		},
		uploadImg(name) {
			uni.chooseImage({
				count: 1,
				success: res => {
					let img = res.tempFiles[0]
					if (img.size > 2097152) {
						this.$app.toast('图片过大，请上传2M以下的图片')
					} else {
						this.$app.upload(img.path, res => {
							this.avatar = res[0]
						})
					}

				}
			});
		},
		loadData() {
			this.$app.request('fans/info', {
				fid: this.fid
			}, res => {
				this.avatar = res.data.avatar
				this.clubname = res.data.clubname
			})
		},

	}
}
